export default {
    props:["user"],
    data(){
        return {
            detailAccount: {}
        }
    },
    mounted(){
        this.getDetailCustomer()
    },
    methods:{
        getDetailCustomer(){
            this.$get(`public/customer/${this.user._id}`)
                .then(res =>{
                    console.log(res)
                    this.detailAccount = res.data
                })
                .catch(err =>{
                    console.log(err)
                })
        },
        openLienHe(){
            this.$refs.refLienHe.show();
            this.$refs.refLienHe.passIDInfor(this.user);
        },

    }
}
